body{
  margin: auto;
}

.body-container {
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: 1fr;
  gap: 20px;
  height: 100vh;
  grid-template-areas: "body-container";
  background-image: url("../../../assets/background-image/fondo-landingPage.jpg");
  background-size: cover;
}



.text {
  display: flex;
  flex-direction: column;
  justify-content: center;
  position: relative;
  left: 55%;
  top: 25%;
  font-family: 'Open Sans', sans-serif;
  padding: 10px;
  width: 40%;
  height: 40%;
}

h1 {
  position: relative;
  top: 5%;
  display: flex;
  justify-content: left;
  font-family: 'Open Sans', sans-serif;
  color: rgb(255, 255, 255);
  font-size: 35px;
  margin: 0px;
}

h2{
  position: relative;
  top: 5%;
  display: flex;
  justify-content: left;
  font-family: 'Open Sans', sans-serif;
  color: white;
  padding-bottom: 0px;
  font-size: 22px;
  letter-spacing: 0px;
  margin-top: 0px;
}

h4{
  display: flex;
  justify-content: left;
  font-family: 'Open Sans', sans-serif;
  color: white;
  font-size: 15px;
  letter-spacing: 2px;
  margin: 0px;
}

.nav-link-container {
  display: inline-block;
}

button {
  position: relative;
  top: 32%;
  padding: 4px;
  color: white;
  background-image: linear-gradient(150deg, #00f7ff, rgb(29, 163, 150), #239029);
  border: 0;
  border-radius: 8px;
  cursor: pointer;
  transition: 0.3s;
}

button span {
  display: flex;
  width: 110px;
  font-size: 25px;
  align-items: center;
  justify-content: center;
  height: 20px;
  text-decoration: none;
  padding: 16px 24px;
  background-color: #111;
  border-radius: 6px;
  transition: 0.2s;
}

button span:hover{
  font-size: 27px;
  font-weight: bold;
}

button:hover span {
  background: none;
}

button:active {
  transform: scale(0.9);
}

@media screen and (max-width: 780px) {
  .body-container {
    display: flex;
    justify-content: center;
    align-items: center;
  }

  button {
    font-size: 16px;
    top: 20px;
  }

  button span:hover {
    font-size: 24px;
  }

  button span{
    width: 100px;
    height: 30px;
  }

  .text {
    display: flex;
    align-items: center;
    justify-content: center;
    left: 50%;
    transform: translate(-50%, 0%);
  }

  h1{
    font-size: 50px;
    font-weight: bold;    
  }

  h2{
    font-size: 24px;
    top: 15px;
    font-weight: inherit;    
  }
  h4{
    display: none;
  }
}
